.resizeable
{
  position: absolute;
  --size:10px;
  height: var(--size);
  width: var(--size);
  user-select: none;
}

.resizeable[data-type='n'],
.resizeable[data-type='s']
{
  width: 100%;
}

.resizeable[data-type='w'],
.resizeable[data-type='e']
{
  height: 100%;
}

.left
{
  left: 0;
}

.right
{
  right: 0;
}

.bottom
{
  bottom: 0;
}

.top
{
  top: 0;
}

.z-10
{
  z-index: 10;
}

.z-20
{
  z-index: 20;
}

.resizeable[data-type='e']
{
    cursor: e-resize;
}

.resizeable[data-type='w']
{
    cursor: w-resize;
}

.resizeable[data-type='s']
{
    cursor: s-resize;
}

.resizeable[data-type='n']
{
    cursor: n-resize;
}

.resizeable[data-type='se']
{
    cursor: se-resize;
}

.resizeable[data-type='sw']
{
    cursor: sw-resize;
}
.resizeable[data-type='ne']
{
    cursor: ne-resize;
}

.resizeable[data-type='nw']
{
    cursor: nw-resize;
}