


.calendar
  --ui-font-color: black
  --calendar-hover-color: hsl(0, 0%, 90%)
  --calendar-today-hover-color:hsl(0, 0%, 90%,.1)
  --calendar-bg-color: hsl(0,0%,95%)
  --calendar-text-blur-color: hsl(0,0%,53%)
  --calendar-text-today-color: white

  font-size: 14px
  background-color: var(--bg-color)
  width: 334px
  height: 404px
  position: absolute
  bottom: 60px
  right: 13px
  display: flex
  flex-flow: column
  user-select: none
  overflow: hidden

  >.header
    width: 100%
    height: 50px
    box-sizing: border-box
    padding: 18px
    background-color: var(--bg-deep-color)

    span
      display: block
      height: 14px
      line-height: 1

  .main
    position: relative
    flex:1
    box-shadow: 0 0 1px black

    .header
      height:54px
      display: flex
      align-items: center

      >div
        height: 34px
        display: flex
        align-items: center
        margin-left: 8px
        border-radius: 2px

        &:hover
          background-color: var(--calendar-hover-color)
        
        &:last-child
          margin-right: 8px

      .text-btn
        flex:1
        padding-left: 8px

      .up,.down
        width:32px
        justify-content: center
        display: flex
        align-items: center
        
        .arrow
          width: 9px
          height: 9px
     
  .page
    height:100%
    width:100%
    display: flex
    flex-flow: column
    position: absolute
    top:0
    left:0
    background-color: var(--bg-color)
    z-index: -1
    transition: .7s
    opacity: 0
    &[data-type='normal']
      z-index: 1
      opacity: 1
    &[data-type='shrink']
      .clips
        transform: scale(0.7,0.7)
    &[data-type='grow']
      .clips
        transform: scale(1.428,1.428)

    .clips
      flex:1
      position: relative
      overflow: hidden
      transition: .4s

  .clip

    background-color: var(--bg-color)
    display: grid
    height: calc(var(--count)/var(--max-count)*100%)
    width: 100%
    grid-template-columns: repeat(var(--rows),1fr)
    grid-template-rows: repeat(var(--count),1fr)
    position: absolute
    top: calc(1/var(--max-count)*var(--top)*100%)
    transition: .4s
    
    .btn
      height: var(--size)
      width: var(--size)
      display: flex
      align-items: center
      justify-content: center
      border-radius: 50%
      color: var(--calendar-text-blur-color)
      font-size: 13px

      &:hover
        background-color: var(--calendar-hover-color)

      &[data-type='focus']
        color: var(--ui-font-color)

      &[data-type='today']
        color: var(--calendar-text-today-color)
        background-color: var(--theme-color)
        position: relative
        overflow: hidden

        &:hover::before
          content: ''
          position: absolute
          height: 100%
          width :100%
          background-color: var(--calendar-today-hover-color)

    .wrap
      display: flex
      align-items: center
      justify-content: center
  
  .weekdays
    height: 40px
    display: grid
    grid-template-columns: repeat(7,1fr)

    .weekday
      display: flex
      justify-content: center
      align-items: center
      