.taskmgr
  --lr-padding: 8px
  user-select: none
  display: flex
  flex-flow: column
  overflow: hidden

  .column
    position: relative
    height: 28px
    font-size: 14px
    display: flex
    align-items: center

    &:hover::before
      content: ''
      position: absolute
      width: 100%
      height: 100%
      z-index: 10
      background-color: rgba(0,118,214,.1)

    &[ui-selected='true']::before
      content: ''
      position: absolute
      width: 100%
      height: 100%
      z-index: 10
      background-color: rgba(0,118,214,.15)

    .icon
      width: 16px
      height: 16px
    
    .name
      padding-left: 5px
      font-size: 12px
      
    
  .column .cell, .tag .cell
    height: 100%
    position: relative
    display: flex
    align-items: center
    border-right: 1px solid #ecd189
    box-sizing: border-box

    &[ui-type='name']
      padding-left: 28px

      &::before
        --border-color: hsl(246, 0%, 50%)
        border-radius: 2px
        --length: 7px
        content: ''
        height: var(--length)
        width: var(--length)
        box-sizing: border-box
        position: absolute
        border-top: 2px solid var(--border-color)
        border-right: 2px solid var(--border-color)
        left: 7px
        transform: rotate(45deg)
    
    &[ui-type='other']
      font-size: 12px
      --back-color: rgb(255,249,228)
      text-align: right
      background-color: var(--back-color)
      padding-right: var(--lr-padding)
      padding-left: var(--lr-padding)
      justify-content: right

    span
      white-space: nowrap
      overflow: hidden
      text-overflow: ellipsi
    >*
    position: relative
    z-index: 1

  .tag
    display: flex
    align-items: center
    color: rgb(31,120,225)
    height: 40px

    .text
      display: flex
      height: 100%
      align-items: center
      padding-left: 10px
      border-box

.taskmgr .head
  display: flex
  align-items: center

  .cell
    --border-color: hsl(246, 0%, 70%)
    position: relative
    box-sizing: border-box
    height: 46px
    margin-top: 10px
    display: flex
    flex-flow: column
    border-right: 1px solid var(--border-color)
    border-bottom: 1px solid var(--border-color)
    text-align: right

    &:hover, &[ui-selected='true']
      background-color: rgba(0,118,214,.1)

    &[ui-type='name']
      padding-left: var(--lr-padding)
      text-align: left

    &[ui-type='other']
      padding-right: var(--lr-padding)

    &[ui-selected='true']::before
      --border-color: hsl(246, 0%, 50%)
      --length: 8px
      border-radius: 2px
      content: ''
      height: var(--length)
      width: var(--length)
      box-sizing: border-box
      position: absolute
      border-top: 2px solid var(--border-color)
      border-right: 2px solid var(--border-color)
      transform-origin: 70.71% 29.29%
      top: 4px
      left: 50%


    &[ui-asc='true']::before
      transform: translateX(-50%) rotate(-45deg)

    &[ui-asc='false']::before
      transform: translateX(-50%) rotate(135deg)

    .text-2
      color: hsl(246, 0%, 40%)
      margin-top: 4px
      font-size: 12px      

.taskmgr .main
  width: 100%
  flex: 1
  overflow: hidden

  .wrap
    width: 100%
    height: 100%

    >.content
      overflow: hidden

.taskmgr .footer
  box-sizing: border-box
  width: 100%
  height: 50px
  border-top: 1px solid hsl(246, 0%, 50%)
  display: flex
  align-items: center
  justify-content: end

  .close-process-btn
    box-sizing: border-box
    height: 22px
    width: 82px 
    border: 1px solid hsl(246, 0%, 80%)
    border-radius: 4px
    margin-right: 10px
    display: flex
    align-items: center
    justify-content: center
    font-size: 13px
    background-color: transparent
    
    &:hover
      border: 1px solid rgb(31,120,225)
      background-color: rgba(0,118,214,.1)

    &:disabled
      border: 1px solid hsl(246, 0%, 80%)
      background-color: hsl(0, 0%, 95%)
      
