.taskbar
  position: absolute
  width: 100%
  height: 48px
  box-sizing: border-box
  padding-left: 12px
  overflow: hidden
  display: flex
  align-items: center
  background: var(--bg-color)
  bottom: 0
  z-index: 1
  user-select: none
  backdrop-filter: blur(10px)

  .left
    height: 100%
    flex:1
    display: flex
    align-items: center

.taskbar .left-btn
  padding: 0 3px

  .img-icon
    --scale: 1
    width: 24px
    height: 24px
    transform: scale(var(--scale))

  &[ui-pointer='down'] .img-icon
    --scale: 83.333333333%

  &[ui-pointer='drag'] .img-icon
    --scale: 1.25

  &:hover .btn,&[ui-pointer='down'] .btn,&[ui-focus='true'] .btn
    box-shadow: 0 0 0 1px var(--bg-deep-color)

  &:hover .btn,&[ui-focus='true'] .btn
    background-color: var(--hover-or-focus-color)
  
  &[ui-pointer='down'] .btn
    background-color: var(--down-color)
  
  &[ui-pointer='drag'] .btn
    background-color: transparent !important
    box-shadow: none !important
    z-index: 3

    &::before
      width: 0px !important
  
  &[ui-focus='true']:hover .btn
    background-color: var(--hover-and-focus-color)

  &[ui-outline='true'] .btn::before
    content: ''
    position: absolute
    margin: 0 auto
    width: 6px
    height: 3px
    background-color: hsl(0, 0%, 54%)
    border-radius: 1.5px
    bottom:1px
    transition: .2s

  &[ui-outline='true'][ui-focus='true'] .btn::before
    width: 16px
    background-color: var(--theme-color)

.taskbar .btn
  position: relative
  padding: 0 7px
  height: 38px
  margin: 5px 0
  display: flex
  align-items: center
  justify-content: center
  border-radius: 4px

.taskbar .start-menu
  .icon
    width: 23px
    height: 23px
    margin-bottom: 1px
    background: linear-gradient(150deg, rgb(126, 220, 254), rgb(13, 155, 252) 100%)
    border-radius: 1px
  
  &[ui-pointer='down'] .icon
    background: rgb(13, 155, 252)
    transform: scale(83.3333333%)

  .clip
    position: absolute
    transform: scale(0)

@keyframes unminimized
  0%
    transform: translateY(0)
  50%
    transform: translateY(-5px)
  90%
    transform: translateY(1px)
  100%
    transform: translateY(0)

@keyframes minimized
  0%
    transform: translateY(0)
  50%
    transform: translateY(5px)
  90%
    transform: translateY(-1px)
  100%
    transform: translateY(0)
    

.taskbar .app-btn
  position: absolute
  transition: left .3s

  &[ui-minimization='false'] .img-icon
    animation: .5s ease-in-out unminimized
  
  &[ui-minimization='true'] .img-icon
    animation: .5s ease-in-out minimized

  &[ui-pointer='drag']
    z-index: 2
    transition: unset

.taskbar .app-btn-bar
  position: relative
  height: 100%
  flex: 1

.taskbar .right
  display: flex

.taskbar .right-btn
  height: 100%
  display: flex
  align-items: center

  &:hover .btn
    background-color: var(--hover-or-focus-color)

  .time
    display: flex
    align-items: end
    flex-flow: column
    padding: 4px 8px
    box-sizing: border-box

    span
      font-size: 10px
      height: 30px
      line-height: 15px