body {
  margin: 0;
  font-family: 'Microsoft YaHei UI',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  width: 100vw;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root
{
  --color:rgb(92,89,117);
  --color-95:hsla(246, 13.6%, 40.4%,.95);
  --color-90:hsla(246, 13.6%, 40.4%,.90);
  --color-s2s-90:hsla(246, 11.6%, 40.4%,.90);
  --color-l5s-90:hsla(246, 13.6%, 35.4%,.90);
}