@keyframes window-create
  0%
    transform: scale(.7)
    transform-origin: center
  99%
    transform: scale(1)
    transform-origin: center
  100%
    transform: scale(1)
    transform-origin: left bottom

.window
  position: absolute
  display: flex
  flex-flow: column
  background-color: var(--bg-color)
  border-radius: 8px
  box-shadow: 0 0 25px rgba(161, 160, 160, 0.4)
  overflow: hidden
  z-index: 1
  transition: .2s ease-out, z-index 0s
  animation: .2s ease-out window-create
  transform-origin: left bottom
  backdrop-filter: blur(10px)

  &[ui-transition='false']
    transition: none

  &[ui-minimization='true']
    transform: translateX(var(--translatex)) translateY(var(--translatey)) scale(0)

  &[ui-maximization='true']
    /*transition: left .2s,top .2s,height .2s, width .2s, transform .2s ease-out*/
    width: 100vw !important
    height: calc(100vh - 48px) !important
    left: 0 !important
    top: 0 !important
    border-radius: 0

    .titlebar .btn[ui-type='maximize']::before
      width: 7px
      height: 7px
      border: 1px solid black
      margin-top: 2px
      margin-right: 2px

    .titlebar .btn[ui-type='maximize']::after
      content: ''
      width: 7px
      height: 7px
      border-top: 1px solid black
      border-right: 1px solid black
      margin-top: -3px
      margin-right: -3px

  .titlebar
    position: relative
    height: 30px
    display: flex
    align-items: center
    user-select: none
    font-size: 13px

    .leftbar
      flex:1
      display: flex
      align-items: center
      height: 100%

      .icon
        --size: 16px
        width: var(--size)
        height: var(--size)
        margin-left: 8px

    .title
      margin-left: 5px
      white-space: nowrap
      overflow: hidden
      text-overflow: ellipsis
    
    .btn
      --hover-color: hsl(0, 0%, 91%)
      position: relative
      display: flex
      height: 100%
      width: 45px
      z-index: 2
      justify-content: center
      align-items: center

      &:hover
        background-color: var(--hover-color)

      &[ui-type='close']:hover
        background-color: rgba(196, 43, 28, .95)
      
      &::before,&[ui-type='close']::after
        position: absolute
        content: ''

      &[ui-type='minimize']::before
        width: 10px
        height: 1px
        background-color: black

      &[ui-type='maximize']::before
        width: 10px
        height: 10px
        border: 1px solid black

      &[ui-type='close']::before,&[ui-type='close']::after
        width: 13px
        height: 1px
        background-color: black

      &[ui-type='close']::before
        transform: rotate(45deg)

      &[ui-type='close']::after
        transform: rotate(-45deg)

.window>.content
  overflow: hidden
  flex: 1
  background: white

  >*
    width: 100%
    height: 100%