.desktop,
.desktop-drag-css
{
  padding: 5px 0 40px;
}

.desktop
{
  color: white;
  background-image: url('/public/resources/img100.jpg');
  background-size: cover;
  /*background: repeating-linear-gradient(90deg, transparent, transparent 75px, black 75px, black 76px),repeating-linear-gradient(0deg, rgb(105, 228, 218),rgb(105, 228, 218) 100px, rgb(180, 86, 86) 100px,rgb(180, 86, 86) 200px);
  background-position-y:-6px;
*/

  background-color: white;
}

.desktop-drag-item,
.desktop-item{
  height: 72px;
  width: 76px;
}

.desktop-item-icon{
  width: 48px;
  height: 48px;
  padding-top:2px;
  margin: 0 auto;
  display: block;
}

.desktop-item-name{
  text-align: center;
  line-height: 1.4;
  margin: 3.5px 2px 0;
  text-shadow: 1px 1px 2px black,
  -1px -1px 2px black;
  font-size: 12px;
  display: block;
}

.desktop-drag-item .css,
.desktop-item .css{
  min-height: 72px;
  border-radius: 3px;
}

.display-none
{
  display: none;
}

.desktop-drag-item
{
  opacity: 0.5;
}

.desktop-item .css:hover{
  background-color:hsla(0,0%,84%,.15);
}

.desktop-item.selected .css{
  background-color:hsla(0,0%,84%,.3);
}

.desktop-item.selected .css:hover {
  background-color:rgba(180, 212, 255, 0.14);
}

.select-range-css{
  --color: hsl(207, 100%, 42%);
  z-index:1;
  width: 100px;
  height: 100px;
  box-sizing: border-box;
  border: 1px solid var(--color);
  background-color: hsla(207, 100%, 42%, .2);

  /*
  background:top / 100% 1px no-repeat repeating-linear-gradient(90deg, black,black 1px,white 1px, white 2px),
  bottom / 100% 1px no-repeat repeating-linear-gradient(270deg, black,black 1px,white 1px, white 2px),
  left / 1px 100% no-repeat repeating-linear-gradient(0deg, black,black 1px,white 1px, white 2px),
  right / 1px 100% no-repeat repeating-linear-gradient(180deg, black,black 1px,white 1px, white 2px);
  */
}