.windows11
  --bg-color: hsla(0, 0%, 95%,.92)
  --bg-deep-color: hsla(0, 0%, 92%,.92)
  --theme-color: rgb(89,85,109)
  --hover-or-focus-color: hsla(0,0%,100%,.48)
  --hover-and-focus-color: hsla(0,0%,100%,.68)
  --down-color: hsla(0,0%,100%,.35)
  position: relative
  width: 100vw
  height: 100vh
  overflow: hidden
  
  >.css-wrap-1,>.css-wrap-1>.css-wrap-2
    width: 100%
    height: 100%
    position: relative
    z-index: 0

.system-window
  transition: transform .3s
  border-radius: 8px
  backdrop-filter: blur(10px)

  &[ui-minimized='right']
    transform: translateX(calc(100% + 13px))

  &[ui-minimized='bottom']
    transform: translateY(calc(100% + 60px))