.start
{

  --padding:32px;
  
  font-size: 12px;

  width: 640px;
  height: 723px;
  left: 13px;
  bottom: 60px;

  position: absolute;
  display: flex;
  flex-flow: column;
  overflow: hidden;
  user-select: none;
  border-radius: 8px;
  background-color: var(--light-start-bg-color);
  box-shadow: 0 0 0 1px hsl(0, 0%, 75%);
  box-sizing: border-box;
  background-color: var(--bg-color);
}


.search .search-bar,
.start .search-bar{
  padding: var(--padding);
}

.search .search,
.start .search{
  --light-search-color:white;

  background-color: var(--light-search-color);
  border-radius: 4px;
  display: flex;
  align-items: center;
  height: 37px;
  box-shadow:0 0 0 1px hsl(0, 0%, 89%), 0 2px 0 var(--theme-color);
}
.search .search .placeholder,
.start .search .placeholder{
  color: hsl(0, 0%, 38%);
  font-size: 14px;
}
.search .search .icon,
.start .search .icon{
  height: 17px;
  width: 17px;
  margin-left: 14px;
  margin-right: 12px;
}
.search .search .icon path,
.start .search .icon path{
  --light-search-icon-color: hsl(0, 0%, 42%);
  fill: var(--light-search-icon-color);
}

.start .app-buttons-bar
{
  color: black;
  flex: 1;
  padding: 0 var(--padding) 0;
  overflow: hidden;
  position: relative;
}

.start .app-buttons-bar .container
{
  height: 100%;
  width: 200%;
  display: flex;
  transition: .3s;
}

.start .app-buttons-bar .page
{
  height: 100%;
  width: 50%;
}

.start .app-buttons-bar .title-bar
{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 32px 12px;
}

.start .app-buttons-bar .title-bar .title
{
  font-size: 15px;
  font-weight: bold;
}

.start .app-buttons-bar .title-bar .button
{
  position: relative;
  font-size: 12px;
  line-height: 12px;
  background-color: hsl(0, 0%, 98%);
  border-radius: 4px;
  padding: 4px 8px;
  
  border: 1px solid hsl(0, 0%, 80%);;
}

.start .app-buttons-bar .title-bar .button:hover
{
  background-color: hsl(0, 0%, 96%);
}

.start .app-buttons-bar .title-bar .button.page1
{
  padding-right: 26px;
}

.start .app-buttons-bar .title-bar .button.page1::before{
  content: '';
  position: absolute;
  box-sizing: border-box;
  width: 6px;
  height: 6px;
  border-top: 1px solid hsl(0, 0%, 40%);
  border-right: 1px solid hsl(0, 0%, 40%);
  transform: rotate(45deg);
  right: 10px;
  top:7px
}

.start .app-buttons-bar .title-bar .button.page2
{
  padding-left: 26px;
}

.start .app-buttons-bar .title-bar .button.page2::before{
  content: '';
  position: absolute;
  box-sizing: border-box;
  width: 6px;
  height: 6px;
  border-top: 1px solid hsl(0, 0%, 40%);
  border-left: 1px solid hsl(0, 0%, 40%);
  transform: rotate(-45deg);
  left: 10px;
  top:7px
}

.start .app-buttons-bar .page1 .buttons
{
  display: grid;
  grid-template-columns: repeat(auto-fill,96px);
  overflow: hidden;
  grid-auto-rows: 84px;
}

.start .app-buttons-bar .page1 .buttons .button
{
  overflow: hidden;
  text-align: center;
  width: 96px;
  height: 84px;
}

.start .app-buttons-bar .page1 .buttons .button .icon
{
  width: 32px;
  height: 32px;
  margin: 12px auto 2px;
  display: block;
}

.start .app-buttons-bar .page1 .buttons .button .name
{
  margin: 0 6px;
  font-size: 12px;
  line-height: 1.4;
  display: block;
  text-overflow: ellipsis;
  height: 38px;
  overflow: hidden;
  line-clamp:2;
}

.start .app-buttons-bar .page2 .buttons
{
  margin: 0 20px;
}

.start .app-buttons-bar .page2 .buttons .button
{
  z-index:1;
  display: flex;
  align-items: center;
  height: 40px;
  padding-left: 12px;
  position: relative;
}

.start .page2 .folder>.button>.css
{
  --size : 7px;
  --border: 1.5px solid hsl(0, 0%, 45%);
  width: var(--size);
  height: var(--size);

  top: 6px;
  right: 12px;
  box-sizing: border-box;
  border-bottom: var(--border);
  border-right: var(--border);
  transform: rotate(45deg);
  position: absolute;
  transform-origin: 70.71067% 70.71067%;
}

.start .page2 .folder[data-expand='true']>.button>.css
{
  transform: rotate(-135deg);
}

.start .page2 .buttons .button .name
{
  margin-left: 10px;
}

.start .page2 .folder>.children
{
  height: 0;
  overflow: hidden;
}

.start .page2 .folder[data-expand='true']>.children
{
  height: auto;
}

.start .page2 .folder>.children>.css
{
  transform: translateY(-100%);
}

.start .page2 .folder[data-expand='true']>.children>.css
{
  transform: translateY(0);
  transition: .4s ease-out;
}

.start .app-buttons-bar .page2 .icon
{
  width: 22px;
  height: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.start .app-buttons-bar .page2 .icon span
{
  font-size: 15px;
}

.start .bottom-bar
{
  --color: hsl(0, 0%, 12%);

  color: var(--color);
  background-color: var(--bg-deep-color);
  padding: 12px var(--padding);
  display: flex;
}

.start .button
{
  border-radius: 4px;
}

.start .button:hover
{
  background-color:var(--hover-or-focus-color) ;
}

.start .button[data-pointer-state='down']
{
  background-color: var(--down-color);
}

.start .bottom-bar .button
{

  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 10px;
}

.start .user-icon
{
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: hsl(0, 0%, 98%);
  border: 1px solid hsl(0, 0%, 93%);
  display: flex;
  align-items: center;
  justify-content: center;
}

.start .user-icon svg
{
  border-radius: 50%;
  height: 18px;
  width: 18px;
  fill: hsl(0, 0%, 56%);
}

.start .button.user span
{
  margin-left:10px;
}

.start .button.system svg
{
  fill: var(--color);
}

.start .bottom-bar .fill
{
  flex: 1;
}